.header {
  width: 100%;
  height: 8vh;
  background-color: #581b98;
  position: fixed;
  top: 0;
  .navBar {
    max-width: 90%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    ul{
      list-style-type: none;
      display: flex;
      height: 100%;
      align-items: center;
     .menu-item{
        display: flex;
       align-items: center;
        text-decoration: none;
        height: 100%;
        color: white;
        font-size: 1.5rem;
        padding: 0 .725em;
        letter-spacing: .08em;
        font-weight: 500;
        &:hover {
          background-color: #4b2477;
        }
        &.active {
          font-weight: 700;
          border-bottom: 4px solid red;
        }
      }
    }
  }
  
}
